import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { moneyToString } from 'utils/formatUtil';
import { getSubsidizeObjects, deleteSubsidizeObject } from 'services/subsidizeObject';

class ListSubsidiesObjectPage extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deleteSubsidizeObject(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const columns = [
      {
        title: 'K hiu',
        dataIndex: 'id',
        key: 'code',
      },
      {
        title: 'Tn i tng',
        dataIndex: 'label',
        key: 'name',
      },
      {
        title: 'S tin tr cp',
        dataIndex: 'supportAmt',
        render: text => moneyToString(text),
        key: 'maxScores',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#subsidies-objects/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    const { reload } = this.state;
    return (
      <Card title="i tng tr cp">
        <CartHeadAction>
          <BtnAddNew href="#/subsidies-objects/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getSubsidizeObjects}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListSubsidiesObjectPage />
  </Page>
);



